import React, { useState } from "react";
// import Avatar from "@material-ui/core/Avatar";
import Button from "@material-ui/core/Button";
import CssBaseline from "@material-ui/core/CssBaseline";
// import TextField from "@material-ui/core/TextField";
// import FormControlLabel from "@material-ui/core/FormControlLabel";
// import Checkbox from "@material-ui/core/Checkbox";
// import Link from "@material-ui/core/Link";
import Grid from "@material-ui/core/Grid";
// import Box from "@material-ui/core/Box";
// import LockOutlinedIcon from "@material-ui/icons/LockOutlined";
// import Typography from "@material-ui/core/Typography";
import { makeStyles } from "@material-ui/core/styles";
// import Container from "@material-ui/core/Container";
import Paper from "@material-ui/core/Paper";
// import DateRangeIcon from "@material-ui/icons/DateRange";
// import MenuItem from "@material-ui/core/MenuItem";
import { Select, Input } from "antd";
import { DatePicker } from "antd";
import { showLoading, hideLoading } from "../actions/loading-action";
import { openAlert } from "../actions/alert-action";

import "./Vacation.css";
import { get, post } from "../../API";
import { useDispatch, useSelector } from "react-redux";
import { TextField } from "@material-ui/core";

const useStyles = makeStyles((theme) => ({
  avatar: {
    margin: theme.spacing(1),
    backgroundColor: theme.palette.secondary.main,
  },
  form: {
    width: "100%", // Fix IE 11 issue.
  },
  submit: {
    backgroundColor: "#93C020",
    color: "#FFF",
    width: "156px",
    boxShadow: "none",
    marginRight: "2.7rem",
    borderRadius: "2px",
    fontWeight: "600",
    marginTop: "22px",
    float: "left",
  },
  paper: {
    padding: 20,
    textAlign: "center",
    color: theme.palette.text.secondary,
    marginRight: 196,
    marginLeft: 35,
    marginTop: 22,
    direction: "rtl",
    border: "0.5px solid #eaeaea",
    boxShadow: "none",
    borderRadius: "2px",
  },
  tableStyle: {
    paddingLeft: 32,
    paddingRight: 32,
    paddingTop: "30px",
  },
  pickerStyle: {
    width: "100%",
    "&:hover": {
      backgroundColor: "#f5f4e8",
    },
  },
}));

var emp_id = 0;
const Vacation = () => {
  const classes = useStyles();
  const dispatch = useDispatch(null);
  const {
    mainData: { processes, processesRequirements },
    userData: { user_role_id },
  } = useSelector((store) => store);

  const [process_id, set_process_id] = useState("1");
  const [reason, set_reason] = useState("");
  const [from, set_from] = useState(null);
  const [to, set_to] = useState(null);
  const [timeFrom, set_timeFrom] = useState(null);
  const [timeTo, set_timeTo] = useState(null);

  const [ar_name, set_ar_name] = useState(null);

  const { TextArea } = Input;

  const { Option } = Select;
  const createVacationRequest = async () => {
    dispatch(showLoading());
    let requests_details = {};
    Object.keys(processesRequirements[process_id]).forEach((key) => {
      if (key === "date" && processesRequirements[process_id][key]) {
        requests_details.from = `${from.toISOString().split("T")[0]} 00:00:00`;
        requests_details.to = `${to.toISOString().split("T")[0]} 23:59:59`;
      }
      if (key === "time" && processesRequirements[process_id][key]) {
        let [firstFrom] = requests_details.from.split(" ");
        let [firstTo] = requests_details.to.split(" ");

        let [
          hoursFrom,
          minutesFrom,
          secondsFrom,
        ] = timeFrom.toISOString().split(".")[0].split("T")[1].split(":");

        let [hoursTo, minutesTo, secondsTo] = timeTo
          .toISOString()
          .split(".")[0]
          .split("T")[1]
          .split(":");

        let tempTimeFrom = `${
          parseInt(hoursFrom) + 2
        }:${minutesFrom}:${secondsFrom}`;
        let tempTimeTo = `${parseInt(hoursTo) + 2}:${minutesTo}:${secondsTo}`;

        requests_details.from = `${firstFrom} ${tempTimeFrom}`;
        requests_details.to = `${firstTo} ${tempTimeTo}`;
      }
      if (key === "reason" && processesRequirements[process_id][key])
        requests_details.reason = reason;
    });

    let { valid = false, msg = "   " } = await post(
      "requests/create_request",
      {
        process_id: parseInt(process_id),
        requests_details,
        ...(emp_id ? { emp_id } : {}),
      }
    );
    if (valid) {
      dispatch(openAlert("success", msg));
    } else {
      dispatch(openAlert("error", msg));
    }
    set_from(null);
    set_to(null);
    set_process_id("1");
    set_reason("");

    dispatch(hideLoading());
  };
  const getEmployee = async () => {
    dispatch(showLoading());
    let {
      valid = false,
      msg = "   ",
      data,
    } = await get("employees/getByName", { ar_name });
    if (valid) {
      set_ar_name(data.ar_name);
      emp_id = data.id;
      dispatch(openAlert("success", msg));
    } else {
      dispatch(openAlert("error", msg));
    }
    dispatch(hideLoading());
  };
  const disableButton = () => {
    const {
      date: dateFlag,
      time: timeFlag,
      reason: reasonFlag,
    } = processesRequirements[process_id];

    return (reasonFlag && reason.trim().length === 0) ||
      (reasonFlag && reason.trim().length < 20) ||
      (reasonFlag && reason.trim().length > 70) ||
      (dateFlag &&
        (from === null ||
          to === null ||
          to < from ||
          (from && from.toISOString() <= new Date().toISOString()) ||
          (to && to.toISOString() <= new Date().toISOString()) ||
          from > to)) ||
      (timeFlag &&
        (timeFrom === null ||
          timeTo === null ||
          timeTo < timeFrom ||
          timeFrom > timeTo)) ||
      (user_role_id === 2 && emp_id === 0)
      ? true
      : false;
  };

  return (
    <Grid item xs={12}>
      <Paper className={classes.paper}>
        <CssBaseline />
        {/* <form className={classes.form} noValidate > */}
        <Grid container spacing={2}>
          <Grid container style={{ marginBottom: "13px" }}>
            <Grid
              item
              lg={1}
              xs={12}
              className="textLabel"
              style={{
                marginTop: "10px",
              }}
            >
              <label> </label>
            </Grid>
            <Grid item xs={12} lg={11}>
              <Select
                size="default"
                placeholder="  "
                style={{ width: "100%", marginLeft: "20px" }}
                value={process_id}
                onChange={(e) => {
                  set_process_id(e.toString());
                }}
              >
                {Object.keys(processes).map((key) => {
                  return <Option key={key.toString()}>{processes[key]}</Option>;
                })}

                {/* <Option key="2"> </Option>
                <Option key="3"> </Option> */}
              </Select>
            </Grid>
          </Grid>

          {user_role_id === 2 && (
            <Grid container style={{ marginBottom: "13px" }}>
              <Grid
                item
                lg={1}
                xs={12}
                className="textLabel"
                style={{
                  marginTop: "10px",
                }}
              >
                <label>  </label>
              </Grid>
              <Grid item xs={12} lg={11}>
                <TextField
                  variant="outlined"
                  required
                  InputProps={{
                    style: { height: "40px", fontSize: "15px" },
                  }}
                  fullWidth
                  InputLabelProps={{
                    style: {
                      direction: "rtl",
                    },
                  }}
                  id="email"
                  name="email"
                  autoComplete="email"
                  value={ar_name}
                  onBlur={() => {
                    if (ar_name) getEmployee();
                  }}
                  onChange={({ target: { value } }) => {
                    emp_id = 0;
                    set_ar_name(value);
                  }}
                />
              </Grid>
            </Grid>
          )}

          {processesRequirements[process_id].date && (
            <Grid container style={{ marginBottom: "13px" }}>
              <Grid
                item
                lg={1}
                className="textLabel"
                xs={12}
                style={{ fontSize: "16px", marginTop: "10px" }}
              >
                <label> </label>
              </Grid>
              <Grid item lg={5} xs={12} className="dateFilter">
                <DatePicker
                  value={from}
                  onChange={(date, dateString) => set_from(date)}
                  placeholder=""
                  style={{ width: "100%", height: "100%" }}
                />
              </Grid>

              <Grid
                item
                lg={1}
                xs={12}
                className="textLabel"
                style={{ fontSize: "16px", marginTop: "10px" }}
              >
                <label> </label>
              </Grid>
              <Grid item lg={5} xs={12} className="dateFilter">
                <DatePicker
                  value={to}
                  onChange={(date, dateString) => set_to(date)}
                  placeholder=""
                  style={{ width: "100%", height: "100%" }}
                />
              </Grid>
            </Grid>
          )}

          {processesRequirements[process_id].time && (
            <Grid container style={{ marginBottom: "13px" }}>
              <Grid
                item
                lg={1}
                className="textLabel"
                xs={12}
                style={{ fontSize: "16px", marginTop: "10px" }}
              >
                <label> </label>
              </Grid>
              <Grid item lg={5} xs={12} className="dateFilter">
                <DatePicker
                  placeholder=""
                  picker={"time"}
                  style={{ width: "100%", height: "100%" }}
                  value={timeFrom}
                  onChange={(date, dateString) => set_timeFrom(date)}
                />
              </Grid>

              <Grid
                item
                lg={1}
                xs={12}
                className="textLabel"
                style={{ fontSize: "16px", marginTop: "10px" }}
              >
                <label> </label>
              </Grid>
              <Grid item lg={5} xs={12} className="dateFilter">
                <DatePicker
                  placeholder=""
                  picker={"time"}
                  style={{ width: "100%", height: "100%" }}
                  value={timeTo}
                  onChange={(date, dateString) => set_timeTo(date)}
                />
              </Grid>
            </Grid>
          )}

          {/* <Grid container style={{ marginBottom: "13px" }}>
            <Grid
              item
              lg={1}
              xs={12}
              className="textLabel"
              style={{ marginTop: "10px" }}
            >
              <label>  </label>
            </Grid>
            <Grid item lg={11} xs={12}>
              <TextField
                placeholder="   "
                variant="outlined"
                required
                InputProps={{
                  style: { height: "40px" },
                }}
                fullWidth
                InputLabelProps={{
                  style: {
                    direction: "rtl",
                  },
                }}
                id="email"
                name="email"
                autoComplete="email"
              />
            </Grid>

            <Grid
                item
                lg={1}
                xs={12}
                style={{ marginTop: "10px" }}
                className="textLabel"
              >
                <label> </label>
              </Grid>
            <Grid item lg={5} xs={12}>
                <TextField
                  variant="outlined"
                  placeholder="   "
                  required
                  InputProps={{
                    style: { height: "40px" },
                  }}
                  fullWidth
                  id="email"
                  name="email"
                  autoComplete="email"
                />
              </Grid>
          </Grid> */}

          <Grid container>
            {processesRequirements[process_id].reason && (
              <>
                <Grid
                  item
                  className="textLabel"
                  lg={1}
                  xs={12}
                  style={{
                    marginTop: "10px",
                  }}
                >
                  <label></label>
                </Grid>
                <Grid item xs={12} lg={11}>
                  <TextArea
                    value={reason}
                    onChange={({ target: { value } }) => {
                      set_reason(value);
                    }}
                    rows={4}
                    placeholder="    20     70 "
                  />
                </Grid>
                <Grid item lg={12}>
                  <div
                    style={{
                      border: "0.3px solid rgb(234 234 234)",
                      marginTop: "104px",
                    }}
                  ></div>
                </Grid>
              </>
            )}
            <Grid item xs={12} sm={12}>
              <Button
                disabled={disableButton()}
                type="submit"
                fullWidth
                variant="contained"
                className={classes.submit}
                onClick={createVacationRequest}
              >
                
              </Button>
            </Grid>
          </Grid>
        </Grid>
        {/* </form> */}
      </Paper>
    </Grid>
  );
};
export default Vacation;
