import React, { Component } from "react";
import Highlighter from "react-highlight-words";
import { Table, Input, Button, Space, Modal } from "antd";
import { FilterOutlined, SearchOutlined, EyeOutlined } from "@ant-design/icons";
import Grid from "@material-ui/core/Grid";
import "./requests.css";
import { connect } from "react-redux";
import { showLoading, hideLoading } from "../actions/loading-action";
import { openAlert } from "../actions/alert-action";
import { get, post } from "../../API";
import translateKey from "../../utilities/TranslatedKeys";
class TableMyRequests extends Component {
  state = {
    searchText: "",
    searchedColumn: "",
    modal2Visible: false,
    modal3Visible: false,
    data: [],
    Requests_details: [],
  };

  componentDidMount = () => {
    this.props.setChildContext(this);
    this.getRequests({});
  };

  setModal2Visible(modal2Visible) {
    this.setState({ modal2Visible });
  }
  setModal3Visible(modal3Visible) {
    this.setState({ modal3Visible });
  }
  handleChange = (pagination, filters, sorter) => {
    this.setState({
      filteredInfo: filters,
      sortedInfo: sorter,
    });
  };

  clearFilters = () => {
    this.setState({ filteredInfo: null });
  };

  clearAll = () => {
    this.setState({
      filteredInfo: null,
      sortedInfo: null,
    });
  };

  setAgeSort = () => {
    this.setState({
      sortedInfo: {
        order: "descend",
        columnKey: "age",
      },
    });
  };

  getColumnSearchProps = (dataIndex) => ({
    filterDropdown: ({
      setSelectedKeys,
      selectedKeys,
      confirm,
      clearFilters,
    }) => (
      <div style={{ padding: 8 }}>
        <Input
          ref={(node) => {
            this.searchInput = node;
          }}
          placeholder={`Search ${dataIndex}`}
          value={selectedKeys[0]}
          onChange={(e) =>
            setSelectedKeys(e.target.value ? [e.target.value] : [])
          }
          onPressEnter={() =>
            this.handleSearch(selectedKeys, confirm, dataIndex)
          }
          style={{ width: 188, marginBottom: 8, display: "block" }}
        />
        <Space>
          <Button
            type="primary"
            onClick={() => this.handleSearch(selectedKeys, confirm, dataIndex)}
            icon={<SearchOutlined />}
            size="small"
            style={{ width: 90 }}
          >
            
          </Button>
          <Button
            onClick={() => this.handleReset(clearFilters)}
            size="small"
            style={{ width: 90 }}
          >
             
          </Button>
        </Space>
      </div>
    ),
    filterIcon: (filtered) => <FilterOutlined style={{ color: "#FFFFFF" }} />,
    onFilter: (value, record) =>
      record[dataIndex]
        ? record[dataIndex]
            .toString()
            .toLowerCase()
            .includes(value.toLowerCase())
        : "",
    onFilterDropdownVisibleChange: (visible) => {
      if (visible) {
        setTimeout(() => this.searchInput.select(), 100);
      }
    },
    render: (text) =>
      this.state.searchedColumn === dataIndex ? (
        <Highlighter
          highlightStyle={{ backgroundColor: "#ffc069", padding: 0 }}
          searchWords={[this.state.searchText]}
          autoEscape
          textToHighlight={text ? text.toString() : ""}
        />
      ) : (
        text
      ),
  });

  handleSearch = (selectedKeys, confirm, dataIndex) => {
    confirm();
    this.setState({
      searchText: selectedKeys[0],
      searchedColumn: dataIndex,
    });
  };

  handleReset = (clearFilters) => {
    clearFilters();
    this.setState({ searchText: "" });
  };

  render() {
    const { TextArea } = Input;
    const { data } = this.state;
    const columns = [
      {
        title: " ",
        dataIndex: "id",
        key: "id",
        ...this.getColumnSearchProps("id"),
        defaultSortOrder: "descend",
        sorter: (a, b) => a.id.length - b.id.length,
      },
      {
        title: "  ",
        dataIndex: "ar_name",
        key: "ar_name",
        ...this.getColumnSearchProps("ar_name"),
        // specify the condition of filtering result
        // here is that finding the name started with `value`
        defaultSortOrder: "descend",
        sorter: (a, b) => a.ar_name - b.ar_name,
      },
      {
        title: "  ",
        dataIndex: "created_at",
        key: "created_at",
        ...this.getColumnSearchProps("created_at"),
        defaultSortOrder: "descend",
        sorter: (a, b) => a.created_at - b.created_at,
      },
      {
        title: " ",
        dataIndex: "process_name",
        key: "process_name",
        ...this.getColumnSearchProps("process_name"),
        // specify the condition of filtering result
        // here is that finding the name started with `value`
        defaultSortOrder: "descend",
        sorter: (a, b) => a.process_name - b.process_name,
      },

      {
        title: " ",
        dataIndex: "state_name",
        key: "state_name",
        ...this.getColumnSearchProps("state_name"),
        defaultSortOrder: "descend",
        sorter: (a, b) => a.state_name.length - b.state_name.length,
      },
      // Action_type_id: 2
      // Action_type_name: ""
      // is_editor: false
      // to_state: 3
      {
        title: "",
        key: "action",
        render: (
          text,
          { actionToTake = [], id, closed = false, emp_id, Requests_details }
        ) =>
          this.getRecordButtons(
            id,
            actionToTake,
            closed,
            emp_id,
            Requests_details
          ),
      },
    ];
    return (
      <>
        <Modal
          centered
          visible={this.state.modal2Visible}
          onOk={() => this.setModal2Visible(false)}
          onCancel={() => this.setModal2Visible(false)}
          footer={[
            <Button key="back" onClick={() => this.setModal2Visible(false)}>
              
            </Button>,
            <Button
              key="submit"
              style={{ backgroundColor: "#93C020", borderColor: "#93C020" }}
              type="primary"
              onClick={() => this.setModal2Visible(false)}
            >
              
            </Button>,
          ]}
        >
          <Grid container style={{ marginTop: "40px" }}>
            <Grid sm={4} style={{ textAlign: "center" }}>
              <label>  </label>
            </Grid>
            <Grid item sm={8}>
              <TextArea rows={4} />
            </Grid>
          </Grid>
        </Modal>

        <Modal
          centered
          visible={this.state.modal3Visible}
          onOk={() => this.setModal3Visible(false)}
          onCancel={() => this.setModal3Visible(false)}
          footer={[
            <Button key="back" onClick={() => this.setModal3Visible(false)}>
              
            </Button>,
            <Button
              key="submit"
              style={{ backgroundColor: "#93C020", borderColor: "#93C020" }}
              type="primary"
              onClick={() => this.setModal3Visible(false)}
            >
              
            </Button>,
          ]}
        >
          {this.state.Requests_details.map(({ key, value }, index) => {
            return (
              <Grid key={index} container style={{ marginTop: "40px" }}>
                <Grid sm={4} style={{ textAlign: "center" }}>
                  <label>{translateKey(key)}</label>
                </Grid>
                <Grid item sm={8}>
                  {value}
                </Grid>
              </Grid>
            );
          })}
        </Modal>
        <Table
          columns={columns}
          rowClassName={(record, index) =>
            index % 2 === 0 ? "table-row-light" : "table-row-dark"
          }
          style={{
            border: "0.5px solid #dcdcdc",
            borderRadius: "2px",
            marginBottom: "2rem",
          }}
          dataSource={data}
          pagination={{
            defaultPageSize: 10,
            position: ["bottomRight"],
            pageSizeOptions: ["10", "20", "30"],
          }}
        />
      </>
    );
  }
  getRecordButtons = (
    request_id,
    actionToTake,
    closed,
    emp_id,
    Requests_details
  ) => {
    if (actionToTake.length !== 0)
      return (
        <Space>
          {actionToTake.map(
            ({ Action_type_name, Action_type_id, to_state }) => (
              <Button
                style={{
                  borderColor: Action_type_id === 2 ? "#93C020" : "red",
                  color: Action_type_id === 2 ? "#93C020" : "red",
                }}
                onClick={() =>
                  this.updateRequest(request_id, Action_type_id, to_state)
                }
              >
                {Action_type_name}
              </Button>
            )
          )}
          <button
            onClick={() => {
              this.setModal3Visible(true);
              this.setState({ Requests_details });
            }}
            id="viewdetails"
          >
            <EyeOutlined />
          </button>
        </Space>
      );
    else
      return (
        <Space>
          <button
            onClick={() => {
              this.setModal3Visible(true);
              this.setState({ Requests_details });
            }}
            id="viewdetails"
          >
            <EyeOutlined />
          </button>
          {!closed && this.props.userData.emp_id === emp_id && (
            <Button danger onClick={() => this.deleteRequest(request_id)}>
               
            </Button>
          )}
        </Space>
      );
  };

  //   <Button danger onClick={() => this.setModal2Visible(true)}>
  //    
  // </Button>
  getRequests = async (filters = {}) => {
    this.props.showLoading();
    let {
      valid = false,
      msg = "   ",
      data,
      // page,
      // per_page,
      // total,
    } = await get("requests/get_requests", { ...filters });
    if (valid) {
      this.setState({ data: data });
      this.props.openAlert("success", msg);
    } else {
      this.setState({ data: [] });
      // this.setState({ data: [], page: 0, per_page: 0, total: 0 });
      this.props.openAlert("error", msg);
    }
    this.props.hideLoading();
  };
  updateRequest = async (request_id, action_id, state_id) => {
    this.props.showLoading();
    let { valid = false, msg = "   " } = await post(
      "requests/update_request",
      {
        request_id,
        action_id,
        state_id,
      }
    );
    if (valid) {
      this.props.openAlert("success", msg);
    } else {
      // this.setState({ data: [], page: 0, per_page: 0, total: 0 });
      this.props.openAlert("error", msg);
    }
    this.getRequests();
    this.props.hideLoading();
  };

  deleteRequest = async (request_id) => {
    this.props.showLoading();
    let { valid = false, msg = "   " } = await post(
      "requests/delete_request",
      {
        request_id,
      }
    );
    if (valid) {
      this.props.openAlert("success", msg);
    } else {
      // this.setState({ data: [], page: 0, per_page: 0, total: 0 });
      this.props.openAlert("error", msg);
    }
    this.getRequests();
    this.props.hideLoading();
  };
}

const mapStateToProps = (state) => {
  return {
    lookUps: state.mainData,
    userData: state.userData,
  };
};

export default connect(mapStateToProps, {
  showLoading,
  openAlert,
  hideLoading,
})(TableMyRequests);
